<!--  -->
<template>
  <div>
    <el-form :model="user" ref="passwordForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="user.loginName" disabled placeholder="" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="oldPassword">
        <el-input v-model="user.oldPassword" type="oldPassword" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="newPassword">
        <el-input v-model="user.newPassword" type="newPassword" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="prePassword">
        <el-input v-model="user.prePassword" type="prePassword" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('passwordForm')"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
  export default {
    name: "passwordForm",
    data() {
      return {
        roleList: [],
        checkRoleList: [""],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        user: {
          loginName:this.$store.getters.userInfo.loginName,
          oldPassword: "",
          newPassword: "",
          prePassword: ""
        },
        //
        rules: {
          oldPassword: [{required: true, message: '', trigger: 'blur'}],
          newPassword: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    mounted() {
    },
    methods: {
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if(this.user.newPassword!=this.user.prePassword){
              this.showWarn("");
              return
            }
            that.$request.doPost('/api/login/modifyPsd', that.user).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess(res.info);
                that.$emit('reloadList');
              }else{
                this.showWarn(res.info)
              }
            });
          }
        });
      },
    }
  }
</script>

<style scoped>
</style>



