<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="userInfo" ref="userInfoForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="avatarUrl">
        <sui-photo :url="userInfo.avatarUrl" v-model="userInfo.avatarUrl" :disabled="dialog.disabled"></sui-photo>
      </el-form-item>
      <el-form-item label="" prop="nickName">
        <el-input v-model="userInfo.nickName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="gender">
        <el-select v-model="userInfo.gender" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in genderDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="country">
        <el-input v-model="userInfo.country" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="province">
        <el-input v-model="userInfo.province" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="city">
        <el-input v-model="userInfo.city" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="openid">
        <el-input v-model="userInfo.openid" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="unionid">
        <el-input v-model="userInfo.unionid" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('userInfoForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {
      suiPhoto,
    },
    name: "userInfoForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        userInfo: {
          avatarUrl: "",
          nickName: "",
          gender: "",
          country: "",
          province: "",
          city: "",
          openid: "",
          unionid: ""
        },
        //
        rules: {
          avatarUrl: [{required: true, message: '', trigger: 'change'}],
          nickName: [{required: true, message: '', trigger: 'blur'}],
          gender: [{required: true, message: '', trigger: 'change'}],
          country: [{required: true, message: '', trigger: 'blur'}],
          province: [{required: true, message: '', trigger: 'blur'}],
          city: [{required: true, message: '', trigger: 'blur'}],
          openid: [{required: true, message: '', trigger: 'blur'}],
          unionid: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      genderDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserInfo(option.id)
        }
      },
      //id
      getUserInfo(id) {
        this.$request.doGet("/api/wxUserInfo/get?id="+id).then(res => {
          if (res.isOk) {
            this.userInfo = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/wxUserInfo/sub', that.userInfo).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userInfoForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



