<!--  -->
<template>
  <div class="app-container">
    <!--  -->
    <message-form ref="messageDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="pid">
        <el-input v-model="queryForm.pid" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item label="" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="" size="small">
          <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="msgType">
        <el-select v-model="queryForm.msgType" placeholder="" size="small" clearable>
          <el-option v-for="dict in msgTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['sysMessage:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysMessage:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="" type="index" width="50"/>
      <el-table-column label="" prop="pid" sortable/>
      <el-table-column label="" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="sysUser in sysUserDict" v-if="sysUser.id==scope.row.userId">{{sysUser.loginName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="msgType" sortable>
        <template slot-scope="scope">
          <span v-for="dict in msgTypeDict" v-if="dict.dictValue==scope.row.msgType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="content" show-overflow-tooltip sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['sysMessage:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['sysMessage:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['sysMessage:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import messageForm from './messageForm'
  export default {
    components: {messageForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        sysUserDict:[],
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          pid: "",
          userId: "",
          msgType: "",
          content: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
      //
      this.getSysUserDict();
    },
    computed: {
      //
      msgTypeDict() {
        return this.$store.getters.getDictArray('msg_type');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/sysMessage/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      getSysUserDict(){
        this.$request.doPost('/api/sysUser/getList', {}).then(res => {
          if (res.isOk) {
            this.sysUserDict=res.list;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      openDialog(onLoad) {
        this.$refs.messageDialog.open(onLoad);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysMessage/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



