<!--  -->
<template>
  <div class="app-container">
    <!--  -->
    <userRegister-form ref="userRegisterDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="realName">
        <el-input v-model="queryForm.realName" placeholder="" size="small"/>
      </el-form-item>
<!--      <el-form-item label="" prop="roleType">-->
<!--        <el-select v-model="queryForm.roleType" placeholder="" size="small" clearable>-->
<!--          <el-option v-for="dict in roleTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['userRegister:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['userRegister:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="" type="index" width="50"/>
      <el-table-column label="" prop="headPicture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.headPicture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="roleType" sortable>
        <template slot-scope="scope">
          <span v-for="dict in roleTypeDict" v-if="dict.dictValue==scope.row.roleType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="wxUserInfoId" sortable>
        <template slot-scope="scope">
          <span v-for="wxUserInfo in wxUserInfoDict" v-if="wxUserInfo.id==scope.row.wxUserInfoId">{{wxUserInfo.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="loginName" sortable/>
      <el-table-column label="" prop="realName" sortable/>
<!--      <el-table-column label="" prop="password" sortable/>-->
      <el-table-column label="" prop="email" sortable/>
      <el-table-column label="" prop="phone" sortable/>
      <el-table-column label="" prop="idCard" sortable/>
      <el-table-column label="" prop="address" show-overflow-tooltip sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['userRegister:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['userRegister:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['userRegister:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import userRegisterForm from './userRegisterForm'
  export default {
    components: {userRegisterForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        wxUserInfoDict:[],
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          headPicture: "",
          roleType: "",
          loginName: "",
          realName: "",
          password: "",
          email: "",
          phone: "",
          idCard: "",
          address: "",
          wxUserInfoId: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
      //id
      this.getWxUserInfoDict();
    },
    computed: {
      //
      roleTypeDict() {
        return this.$store.getters.getDictArray('role_type');
      },
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/wxUserRegister/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //id
      getWxUserInfoDict(){
        this.$request.doPost('/api/wxUserInfo/getList', {}).then(res => {
          if (res.isOk) {
            this.wxUserInfoDict=res.list;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      openDialog(onLoad) {
        this.$refs.userRegisterDialog.open(onLoad);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/wxUserRegister/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



