<!--  -->
<template>
  <el-dialog :title="dialog.title" center style="margin-top: 200px" width="500px" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="sysUser.password" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="prePassword">
        <el-input v-model="sysUser.prePassword" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitRegister('sysUserForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          prePassword:"",
          password: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          prePassword: [{required: true, message: '', trigger: 'blur'}],
        }
      }
    },
    methods: {

      //
      open(option) {
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitRegister: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if(that.sysUser.password!=that.sysUser.prePassword){
              this.showWarn("!")
              return false
            }
            that.$request.doPost('/api/login/register',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }else{
                that.showWarn(res.info);
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



