<!--  -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="notice" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="id" class="hidden" prop="id">
              <el-input type="hidden" v-model="notice.id"></el-input>
            </el-form-item>
            <el-form-item label="" prop="picture">
                <sui-photo :url="notice.picture" v-model="notice.picture" :disabled="dialog.disabled"></sui-photo>
            </el-form-item>
            <el-form-item label="" prop="title">
                <el-input v-model="notice.title" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="notice.type" placeholder="" :disabled="dialog.disabled" >
                    <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="publishTime">
                <el-date-picker v-model="notice.publishTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd" />
            </el-form-item>
            <el-form-item label="" prop="content" >
                <el-input type="textarea" v-model="notice.content" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('noticeForm')"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
    import suiPhoto from "@/components/sui-photo/sui-photo";
    export default {
        components: {
            suiPhoto,
        },
        name: "noticeForm",
        data() {
          return {
            //
            dialog: {
                title: "",
                open: false,
                disabled: false
            },
            //
            notice: {
                picture: "",
                title: "",
                type: "",
                publishTime: "",
                content: ""
            },
            //
            rules: {
                picture: [{required: true, message: '', trigger: 'change'}],
                title: [{required: true, message: '', trigger: 'blur'}],
                type: [{required: true, message: '', trigger: 'change'}],
                publishTime: [{required: true, message: '', trigger: 'change'}],
            }
          }
        },
        computed: {
            //
            typeDict() {
                return this.$store.getters.getDictArray('notice_type');
            },
        },
        methods: {
            //
            open(option) {
              const pageFrom = option.pageFrom;
              const disabled = pageFrom == 'view' ? true : false;
              this.showDialog(option.title, disabled);
              if (pageFrom == 'edit' || pageFrom == 'view') {
                //
                this.getNotice(option.id)
              }
            },
            //
            cancel: function () {
                this.dialog.open = false
            },
            //id
            getNotice(id){
              this.$request.doGet("/api/notice/get?id="+id).then(res => {
                if (res.isOk) {
                  this.notice = res.obj;
                }
              });
            },
            //
            submitForm: function (formName) {
                const that = this;
                that.$refs[formName].validate((valid) => {
                    if (valid) {//
                        that.$request.doPost('/api/notice/sub', that.notice).then(res => {
                            if (res.isOk) {
                                that.dialog.open = false;
                                that.showSuccess("");
                                that.$emit('reloadList');
                            }
                        });
                    }
                });
            },
            //
            showDialog(title, disabled) {
                this.resetForm(this.$refs.noticeForm);//
                this.dialog.open = true;//
                this.dialog.title = title;//
                this.dialog.disabled = disabled;//
            }
        }
    }
</script>

<style scoped>
</style>



