<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysAttach" ref="sysAttachForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="attachType">
        <el-input v-model="sysAttach.attachType" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="fileType">
        <el-input v-model="sysAttach.fileType" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="savePath">
        <el-input v-model="sysAttach.savePath" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="fileName">
        <el-input v-model="sysAttach.fileName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="fileSize">
        <el-input v-model="sysAttach.fileSize" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="id" prop="refId">
        <el-input v-model="sysAttach.refId" placeholder="id" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="suffix">
        <el-input v-model="sysAttach.suffix" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysAttachForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysAttachForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysAttach: {
          attachType: "",
          fileType: "",
          savePath: "",
          fileName: "",
          fileSize: "",
          refId: "",
          suffix: ""
        },
        //
        rules: {
          attachType: [{required: true, message: '', trigger: 'blur'}],
          fileType: [{required: true, message: '', trigger: 'blur'}],
          savePath: [{required: true, message: '', trigger: 'blur'}],
          fileName: [{required: true, message: '', trigger: 'blur'}],
          fileSize: [{required: true, message: '', trigger: 'blur'}],
          refId: [{required: true, message: 'id', trigger: 'blur'}],
          suffix: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysAttach = res.obj;
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysAttach = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysAttach/sub', that.sysAttach).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysAttachForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



