<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
        <el-select v-model="sysUser.sex" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleList">
        <el-checkbox-group v-model="checkRoleList">
          <el-checkbox v-for="(item,index) in roleList" :disabled="dialog.disabled"  border size="medium" :key="index" :label="item" >{{item.roleName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysUserForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        checkRoleList: [""],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          photo: "",
          loginName: "",
          password: "",
          userName: "",
          userNo: "",
          email: "",
          phone: "",
          officeId: ""
        },
        //
        rules: {
          photo: [{required: true, message: '', trigger: 'blur'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          sex: [{required: true, message: '', trigger: 'change'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
          phone: [{required: true, message: '', trigger: 'blur'}],
          officeId: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    mounted() {
    },
    computed: {
      //
      sexDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
    },
    methods: {
      getRoleList(userId){
        const that = this;
        that.$request.doGet('/api/sysUser/findRolesByUserId?userId='+userId).then(res => {
          if (res.isOk) {
            that.roleList = res.list;
            this.checkRoleList = that.roleList.filter(function (v) {
              if (v.checked) {return v}
            });
          }
        });
      },
      //
      openDialogAdd(title) {
        this.getRoleList()
        this.showDialog(title, false);
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysUser = res.obj;
            this.getRoleList(this.sysUser.id)
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            if(that.checkRoleList.length==0){
              that.showWarn("");
              return false
            }
            that.sysUser.roleList=that.checkRoleList.map(function (v) {
              return v.roleId
            });
            that.$request.doPost('/api/sysUser/sub', that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



