<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysQuartz" ref="sysQuartzForm" :rules="rules" label-width="100px">
        <el-form-item label="" prop="params">
          <el-input v-model="sysQuartz.params" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cronExpression">
          <el-input v-model="sysQuartz.cronExpression" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="jobStatus">
          <el-input v-model="sysQuartz.jobStatus" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="isExec">
          <el-input v-model="sysQuartz.isExec" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="isBootExec">
          <el-input v-model="sysQuartz.isBootExec" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="beanName">
          <el-input v-model="sysQuartz.beanName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="jobName">
          <el-input v-model="sysQuartz.jobName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="lastExecTime">
          <el-input v-model="sysQuartz.lastExecTime" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="hasParams">
          <el-input v-model="sysQuartz.hasParams" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="methodName">
          <el-input v-model="sysQuartz.methodName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="jobType">
          <el-input v-model="sysQuartz.jobType" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input v-model="sysQuartz.remarks" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysQuartzForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    name: "sysQuartzForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysQuartz: {
          params: "",
          cronExpression: "",
          jobStatus: "",
          isExec: "",
          isBootExec: "",
          beanName: "",
          jobName: "",
          lastExecTime: "",
          hasParams: "",
          methodName: "",
          jobType: "",
          remarks: ""
        },
        //
        rules: {
          params: [{required: true, message: '', trigger: 'blur'}],
          cronExpression: [{required: true, message: '', trigger: 'blur'}],
          jobStatus: [{required: true, message: '', trigger: 'blur'}],
          isExec: [{required: true, message: '', trigger: 'blur'}],
          isBootExec: [{required: true, message: '', trigger: 'blur'}],
          beanName: [{required: true, message: '', trigger: 'blur'}],
          jobName: [{required: true, message: '', trigger: 'blur'}],
          lastExecTime: [{required: true, message: '', trigger: 'blur'}],
          hasParams: [{required: true, message: '', trigger: 'blur'}],
          methodName: [{required: true, message: '', trigger: 'blur'}],
          jobType: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      openDialogAdd(title) {
        this.showDialog(title, false);
      },
      //
      openDialogView(title,url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, true);
            this.sysQuartz = res.obj;
          }
        });
      },
      //
      openDialogEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysQuartz = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysQuartz/sub', that.sysQuartz).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysQuartzForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



