<template>
  <div>
    <img src="/static/images/message/bba_thumb.gif" @click="onclick" />
    <ul v-if="show" :class="['face',{'aside-hidden': show}]">
      <li v-for="item in gifList"><img @click="selectFace(item)" :src=getGifUrl(item.gif) :title="item.title"></li>
    </ul>
  </div>
</template>
<script>
  export default{
    props: {
      show: {
        type: Boolean,
        default: false
      }
    },
    data(){
      return{
        gifList: [
          {gif: "smilea_thumb.gif", title: "[]"},
          {gif: "tootha_thumb.gif", title: "[]"},
          {gif: "laugh.gif", title: "[]"},
          {gif: "tza_thumb.gif", title: "[]"},
          {gif: "kl_thumb.gif", title: "[]"},
          {gif: "kbsa_thumb.gif", title: "[]"},
          {gif: "cj_thumb.gif", title: "[]"},
          {gif: "shamea_thumb.gif", title: "[]"},
          {gif: "zy_thumb.gif", title: "[]"},
          {gif: "bz_thumb.gif", title: "[]"},
          {gif: "bs2_thumb.gif", title: "[]"},
          {gif: "lovea_thumb.gif", title: "[]"},
          {gif: "sada_thumb.gif", title: "[]"},
          {gif: "heia_thumb.gif", title: "[]"},
          {gif: "qq_thumb.gif", title: "[]"},
          {gif: "sb_thumb.gif", title: "[]"},
          {gif: "mb_thumb.gif", title: "[]"},
          {gif: "ldln_thumb.gif", title: "[]"},
          {gif: "yhh_thumb.gif", title: "[]"},
          {gif: "zhh_thumb.gif", title: "[]"},
          {gif: "x_thumb.gif", title: "[]"},
          {gif: "cry.gif", title: "[]"},
          {gif: "wq_thumb.gif", title: "[]"},
          {gif: "t_thumb.gif", title: "[]"},
          {gif: "k_thumb.gif", title: "[]"},
          {gif: "bba_thumb.gif", title: "[]"},
          {gif: "angrya_thumb.gif", title: "[]"},
          {gif: "yw_thumb.gif", title: "[]"},
          {gif: "cza_thumb.gif", title: "[]"},
          {gif: "88_thumb.gif", title: "[]"},
          {gif: "sk_thumb.gif", title: "[]"},
          {gif: "sweata_thumb.gif", title: "[]"},
          {gif: "sleepya_thumb.gif", title: "[]"},
          {gif: "sleepa_thumb.gif", title: "[]"},
          {gif: "money_thumb.gif", title: "[]"},
          {gif: "sw_thumb.gif", title: "[]"},
          {gif: "cool_thumb.gif", title: "[]"},
          {gif: "hsa_thumb.gif", title: "[]"},
          {gif: "hatea_thumb.gif", title: "[]"},
          {gif: "gza_thumb.gif", title: "[]"},
          {gif: "dizzya_thumb.gif", title: "[]"},
          {gif: "bs_thumb.gif", title: "[]"}
        ],
      }
    },
    methods:{
      onclick(){
        this.show=!this.show
      },
      getGifUrl(url){
        return '/static/images/message/'+url
      },
      selectFace(item){
        let img = "<img style='vertical-align:middle' src='/static/images/message/"+item.gif+"' title='["+item.title+"]'>"
        this.$emit('selectFace',img)
      }
    }
  }
</script>
<style scoped>
  .face{
    width: 500px;
    list-style-type: none;
  }
  .face li{
    padding: 5px;
    float: left;
  }
</style>



