<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <div style="height: 50vh;overflow: auto;">
    <el-tree
      :data="treeData"
      show-checkbox
      node-key="id"
      :default-expanded-keys="['239acec3d2354b4dae5b042dbb5e25e2']"
      ref="tree"
      highlight-current
      :props="defaultProps">
    </el-tree>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" type="primary" @click="getCheckedNodes"> </el-button>
    </div>
  </el-dialog>

</template>

<script>
  export default {
    methods: {
      getMenuRoleTree(roleId) {
        const that = this;
        this.$request.doGet('/api/sysRole/getMenuRoleTree?roleId=' + roleId).then(res => {
          if (res.isOk) {
            this.treeData = res.list;
            setTimeout(function () {
              that.setCheckedNodes(that.treeData);
            });
            console.log(res.list)
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
      //
      openDialogMenuEdit(title, url) {
        this.$request.doGet(url).then(res => {
          if (res.isOk) {
            this.showDialog(title, false);
            this.sysRole = res.obj;
            this.getMenuRoleTree(res.obj.id)
          }
        });
      },
      getCheckedNodes() {
        //
        const checkedNodes =  this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys())
        this.$request.doPost('/api/sysRole/updateMenuRoleTree', {
          roleId: this.sysRole.id,
          menuIds: checkedNodes
        }).then(res => {
          if (res.isOk) {
            this.showSuccess("");
            this.dialog.open = false;
          }
        });
      },
      getNodes(checkList, treeData) {
        treeData.filter(v => {
          if (v.children) {
            let itmes = v.children;
            itmes.forEach(node => {
              if (node.children) {
                this.getNodes(checkList, node.children)
              } else {
                if (node.checked) {
                  checkList.push(node)
                }
              }
            })
          } else {
            if (v.checked) {
              checkList.push(v)
            }
          }
        });
      },
      //
      setCheckedNodes(treeData) {
        let checkList = [];
        this.getNodes(checkList, treeData);
        this.$refs.tree.setCheckedNodes(checkList);
      },
    },

    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        sysRole: {},
        treeData: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    }
  };
</script>

<style scoped>
</style>



