<!--  -->
<template>
  <div class="app-container">
    <!--  -->
    <sysAttach-form ref="sysAttachDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="attachType">
        <el-input v-model="queryForm.attachType" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item label="" prop="fileName">
        <el-input v-model="queryForm.fileName" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item label="" prop="suffix">
        <el-input v-model="queryForm.suffix" placeholder="" size="small"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="handleAdd" v-hasPermi="['sysAttach:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['sysAttach:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="" type="index" width="50"/>
      <el-table-column label="" prop="attachType" sortable />
      <el-table-column label="" prop="fileType" sortable />
      <el-table-column label="" prop="savePath" sortable />
      <el-table-column label="" prop="fileName" sortable />
      <el-table-column label="" prop="fileSize" sortable />
      <el-table-column label="id" prop="refId" sortable />
      <el-table-column label="" prop="suffix" sortable />
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="handleView(scope.row)" v-hasPermi="['sysAttach:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="handleUpdate(scope.row)" v-hasPermi="['sysAttach:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row)" v-hasPermi="['sysAttach:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.pageNum"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import sysAttachForm from './attachForm'

  export default {
    components: {sysAttachForm},
    data() {
      return {
        loading: true,//
        selectedData: [],//
        total: 0, //
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          attachType: "",
          fileType: "",
          savePath: "",
          fileName: "",
          fileSize: "",
          refId: "",
          suffix: ""
        }
      };
    },
    mounted() {
      //
      this.getList();
    },
    computed: {
    },
    methods: {
      //
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //
      getList() {
        this.$request.doPost('/api/sysAttach/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //
      handleAdd() {
        this.$refs.sysAttachDialog.openDialogAdd('');
      },
      //
      handleView(row) {
        this.$refs.sysAttachDialog.openDialogView('', '/api/sysAttach/get?id=' + row.id);
      },
      //
      handleUpdate(row) {
        this.$refs.sysAttachDialog.openDialogEdit('', '/api/sysAttach/get?id=' + row.id);
      },
      //
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //
      handleDeleteAll() {
        const that = this;
        that.showConfirm('?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("")
          } else {
            that.showWarn("")
          }
        });
      },
      //
      handleDelete(row) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(row.id);
          that.showSuccess("");
        });
      },
      //
      delete(id) {
        this.$request.doGet('/api/sysAttach/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



