<!--  -->
<template>
  <el-dialog  width="500px" center :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.password" type="password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="login"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  import {mapActions} from "vuex";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        roleList: [],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysUser: {
          loginName: "",
          password: ""
        },
        //
        rules: {
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['commitClientLoginInfo']),
      initData() {
        const loading = this.$loading({
          lock: true,
          text: ',...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
      },
      login() {
        const loading = this.$loading({
          lock: true,
          text: ',...',
          spinner: 'el-icon-loading',
          background: 'rgba(0,0,0,0.25)'
        });
        this.$request.doPost("/api/client/login/userLogin", this.sysUser).then(res => {
          if (res.isOk) {
              //
              this.commitClientLoginInfo().then(res => {
                loading.close();
                this.dialog.open = false;
                this.showSuccess("");
              })
          } else {
            this.showWarn(res.info);
          }
        });
      },
      //
      open(option) {
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      showDialog(title, disabled) {
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
</style>



