<!--  -->
<template>
  <div style="height: 100vh;overflow-y: auto;padding:0 100px;">
    <notice-form ref="noticeDialog"></notice-form>
    <div v-if="dataList.length>0">
      <el-row style="background: #efefef;">
        <el-col style="padding: 10px" v-for="(notice, index) in dataList" :key="index" :span="6">
            <el-card  :body-style="{ padding: '10px'}">
              <img :src="formatUrl(notice.picture)" class="image">
              <div style="padding: 14px;">
                <span>{{notice.title}}</span>
                <div class="bottom">
                  <time class="time">{{notice.publishTime}}</time>
                  <div style="float: right">
                    <el-button size="mini" type="normal" @click="openDialog({title:'',pageFrom:'view',id:notice.id})"></el-button>
                    <el-button size="mini" type="primary" @click="openDialog({title:'',pageFrom:'edit',id:notice.id})"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(notice.id)"></el-button>
                  </div>
                </div>
              </div>
            </el-card>
        </el-col>
      </el-row>
      <el-pagination
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-size="queryForm.size"
        layout="total, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <el-empty v-else description=""></el-empty>
  </div>

</template>

<script>
  import noticeForm from '@/pages/module/notice/noticeForm'
  export default {
    components: {noticeForm},
    name: "noticeService",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        dataList:[],
        total: 0, //
        //
        queryForm: {
          current: 1,
          size: 8,
          picture: "",
          title: "",
          type: "",
          publishTime: "",
          content: ""
        },
        //
        rules: {
        }
      }
    },
    mounted() {
      this.getList()
    },
    methods: {
      //
      openDialog(option) {
        this.$refs.noticeDialog.open(option);
      },
      //
      handleDelete(id) {
        const that = this;
        that.showConfirm('?', function () {
          that.delete(id);
          that.showSuccess("");
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      getList() {
        this.$request.doPost('/api/client/notice/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //
      delete(id) {
        this.$request.doGet('/api/client/notice/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysUserForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      },
    }
  }
</script>

<style scoped>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    height: 200px;
    width: 100%;
    display: block;
  }
</style>



