<!--  -->
<template>
  <div>
    <el-form :model="sysUser" ref="sysUserForm" :rules="rules" label-width="100px">
      <el-form-item label="2" prop="loginName">
        <sui-photo v-model="sysUser.photo" :disabled="dialog.disabled" :url="sysUser.photo">11</sui-photo>
      </el-form-item>
      <el-form-item label="" prop="loginName">
        <el-input v-model="sysUser.loginName" placeholder="" :disabled="true" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userName">
        <el-input v-model="sysUser.userName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="sysUser.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="sysUser.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" @click="submitForm('sysUserForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {suiPhoto},
    name: "sysUserForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        rules: {
        }
      }
    },
    mounted() {
    },
    computed:{
      sysUser(){
         return this.$store.getters.userInfo;
      }
    },
    methods: {
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/login/updateUserInfo',that.sysUser).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
    }
  }
</script>

<style scoped>
</style>



