<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="userRegister" ref="userRegisterForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="headPicture">
        <sui-photo :url="userRegister.headPicture" v-model="userRegister.headPicture" :disabled="dialog.disabled"></sui-photo>
      </el-form-item>
      <el-form-item label="" prop="wxUserInfoId">
        <el-select v-model="userRegister.wxUserInfoId" placeholder="" :disabled="dialog.disabled">
          <el-option v-for="wxUserInfo in wxUserInfoDict" :key="wxUserInfo.id" :value="wxUserInfo.id" :label="wxUserInfo.nickName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="roleType">
        <el-select v-model="userRegister.roleType" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in roleTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="loginName">
        <el-input v-model="userRegister.loginName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="realName">
        <el-input v-model="userRegister.realName" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
<!--      <el-form-item label="" prop="password">-->
<!--        <el-input v-model="userRegister.password" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>-->
<!--      </el-form-item>-->
      <el-form-item label="" prop="email">
        <el-input v-model="userRegister.email" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="userRegister.phone" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="idCard">
        <el-input v-model="userRegister.idCard" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="address" >
        <el-input type="textarea" v-model="userRegister.address" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('userRegisterForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {
      suiPhoto,
    },
    name: "userRegisterForm",
    data() {
      return {
        wxUserInfoDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        userRegister: {
          headPicture: "",
          roleType: "",
          loginName: "",
          realName: "",
          password: "",
          email: "",
          phone: "",
          idCard: "",
          address: "",
          wxUserInfoId: ""
        },
        //
        rules: {
          headPicture: [{required: true, message: '', trigger: 'change'}],
          roleType: [{required: true, message: '', trigger: 'change'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          realName: [{required: true, message: '', trigger: 'blur'}],
          // password: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
          phone: [{required: true, message: '', trigger: 'blur'}],
          idCard: [{required: true, message: '', trigger: 'blur'}],
          wxUserInfoId: [{required: true, message: '', trigger: 'change'}]
        }
      }
    },
    computed: {
      //
      roleTypeDict() {
        return this.$store.getters.getDictArray('role_type');
      },
    },
    methods: {
      //id
      getWxUserInfoDict(){
        this.$request.doPost('/api/wxUserInfo/getList', {}).then(res => {
          if (res.isOk) {
            this.wxUserInfoDict=res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getWxUserInfoDict();//id
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserRegister(option.id)
        }
      },
      //id
      getUserRegister(id) {
        this.$request.doGet("/api/wxUserRegister/get?id="+id).then(res => {
          if (res.isOk) {
            this.userRegister = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/wxUserRegister/sub', that.userRegister).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userRegisterForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



