<!--  -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysMessage" ref="sysMessageForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="" prop="pid">
        <el-input v-model="sysMessage.pid" placeholder="" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="" prop="userId">
        <el-select v-model="sysMessage.userId" placeholder="" :disabled="dialog.disabled">
          <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="msgType">
        <el-select v-model="sysMessage.msgType" placeholder="" :disabled="dialog.disabled" >
          <el-option v-for="dict in msgTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="content" >
        <el-input type="textarea" v-model="sysMessage.content" placeholder="" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel"> </el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysMessageForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    components: {

    },
    name: "sysMessageForm",
    data() {
      return {
        sysUserDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        sysMessage: {
          pid: "",
          userId: "",
          msgType: "",
          content: ""
        },
        //
        rules: {
          pid: [{required: true, message: '', trigger: 'blur'}],
          userId: [{required: true, message: '', trigger: 'change'}],
          msgType: [{required: true, message: '', trigger: 'change'}],
        }
      }
    },
    computed: {
      //
      msgTypeDict() {
        return this.$store.getters.getDictArray('msg_type');
      },
    },
    methods: {
      //
      getSysUserDict(){
        this.$request.doPost('/api/sysUser/getList', {}).then(res => {
          if (res.isOk) {
            this.sysUserDict=res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysUserDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysMessage(option.id)
        }
      },
      //id
      getSysMessage(id) {
        this.$request.doGet("/api/sysMessage/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysMessage = res.obj;
          }
        });
      },
      //
      cancel: function () {
        this.dialog.open = false
      },
      //
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//
            that.$request.doPost('/api/sysMessage/sub', that.sysMessage).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysMessageForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>
</style>



