<!--  -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="userMsg" ref="userMsgForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="id" class="hidden" prop="id">
                <el-input type="hidden" v-model="userMsg.id"/>
            </el-form-item>
            <el-form-item label="" prop="picture">
                <sui-photo type="userMsg" v-model="userMsg.picture" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="" prop="userId">
                <el-select v-model="userMsg.userId" placeholder="" :disabled="dialog.disabled">
                    <el-option v-for="wxUserRegister in wxUserRegisterDict" :key="wxUserRegister.id" :value="wxUserRegister.id" :label="wxUserRegister.loginName"/>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="sex">
                <el-select v-model="userMsg.sex" placeholder="" :disabled="dialog.disabled" >
                    <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="title">
                <el-input v-model="userMsg.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="" prop="msgDate">
                <el-date-picker v-model="userMsg.msgDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="" prop="content">
                <el-input type="textarea" v-model="userMsg.content" placeholder="" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="" prop="fileList">
                <sui-file type="userMsg" :refId="userMsg.id" v-model="userMsg.fileList" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel"> </el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('userMsgForm')"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
import suiPhoto from "@/components/sui-photo/sui-photo";
import suiFile from "@/components/sui-file/sui-file";
export default {
    components: {
        suiFile,suiPhoto,
    },
    name: "userMsgForm",
    data() {
      return {
        wxUserRegisterDict:[],
        //
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //
        userMsg: {
            id: "",
            picture: "",
            userId: "",
            sex: "",
            title: "",
            msgDate: "",
            content: ""
        },
        //
        rules: {
            picture: [{required: true, message: '', trigger: 'change'}],
            userId: [{required: true, message: '', trigger: 'change'}],
            sex: [{required: true, message: '', trigger: 'change'}],
            title: [{required: true, message: '', trigger: 'blur'}],
            msgDate: [{required: true, message: '', trigger: 'change'}],
            content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //
        sexDict() {
            return this.$store.getters.getDictArray('sys_sex');
        },
    },
    methods: {
        //
        getWxUserRegisterDict(){
            this.$request.doPost('/api/wxUserRegister/getList', {}).then(res => {
                if (res.isOk) {
                    this.wxUserRegisterDict=res.list;
                }
            })
        },
        //
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            this.getWxUserRegisterDict();//
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getUserMsg(option.id)
            }
        },
        //id
        getUserMsg(id) {
            this.$request.doGet("/api/userMsg/get?id="+id).then(res => {
                if (res.isOk) {
                    this.userMsg = res.obj;
                }
            });
        },
        //
        cancel: function () {
            this.dialog.open = false
        },
        //
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//
                    that.$request.doPost('/api/userMsg/sub', that.userMsg).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //
        showDialog(title, disabled) {
            this.resetForm(this.$refs.userMsgForm);//
            this.dialog.open = true;//
            this.dialog.title = title;//
            this.dialog.disabled = disabled;//
        }
    }
}
</script>

<style scoped>
</style>



